import React, { useRef, useState } from "react";

import { Modal, ModalBody, ModalFooter, ModalHeader, ModalHeading } from "../Modal";

import { NotificationBodyTypography, NotificationToast, ToastContainer } from "./Toast.styles";
import { ToastModalProps } from "./Toast.types";
import { useToast } from "./ToastContext";


const Toast = () => {
	const { notifications } = useToast();
	const toastContainerRef = useRef<HTMLDivElement>(null);

	const [detailsShown, setDetailsShown] = useState<boolean>(false);
	const [modalProps, setModalProps] = useState<ToastModalProps>({
		modalTitle: "",
		modalDetails: "",
		modalButtonText: "",
	});
	

	const hideDetails = () => {
		setDetailsShown(false);
		setModalProps({ modalTitle: "", modalDetails: "", modalButtonText: "" });
	};

	const showDetails = (title: string | undefined, details: string, buttonText: string | undefined) => {
		setModalProps({
			modalTitle: title || "Details",
			modalDetails: details,
			modalButtonText: buttonText || "Close" });
		setDetailsShown(true);
	};

	return (
		<ToastContainer ref={toastContainerRef}>
			{detailsShown && <Modal
				isOpen={detailsShown}
				dataQA="notification-toast-details"
				contentLabel="Details"
				onRequestClose={hideDetails}
				variant="scrollable"
				appElement={toastContainerRef.current || document.body}
			>
				<ModalHeader dataQA="notification-toast">
					<ModalHeading
						component="h2"
						dataQA="notification-toast"
						headingIcon="Info"
					>
						{modalProps.modalTitle}
					</ModalHeading>
				</ModalHeader>
				<ModalBody dataQA="notification-toast">
					<NotificationBodyTypography
						dataQA="notification-toast-paragraphy"
						variant="bodyCopy"
					>
						{modalProps.modalDetails}
					</NotificationBodyTypography>
				</ModalBody>
				<ModalFooter
					dataQA="notification-toast"
					primaryButtonText={modalProps.modalButtonText}
					primaryButtonOnClick={() => hideDetails()}
					secondaryButtonText=""
					hideSecondaryButton
				/>
			</Modal>}
			{notifications.map((notification) => (
				<NotificationToast
					key={notification.id}
					id={notification.id}
					dataQA={notification.dataQA}
					className={notification.className}
					text={notification.text}
					variant={notification.variant}
					showIcon={notification.showIcon}
					trailingLinkText={notification.trailingLinkText}
					trailingLinkAction={() =>
						notification.modalDetails
							? showDetails(notification.modalTitle, notification.modalDetails, notification.modalButtonText)
							: notification.trailingLinkAction && notification.trailingLinkAction()
					}
					iconName={notification.iconName}
					iconColor={notification.iconColor}
					textColor={notification.textColor}
					backgroundColor={notification.backgroundColor}
					hasTimeout={notification.hasTimeout}
					timeout={notification.timeout}
					onTimeout={notification.onTimeout}
					onDismiss={notification.onDismiss}
					$zIndex={notification.$zIndex}
				/>
			))}
		</ToastContainer>
	);
};

Toast.displayName = "Toast";
export default Toast;
